@font-face { /* 애플산돌고딕M 폰트 적용 */
  font-family: "SDGothicM";
  src: url("./Apple_Neo/AppleSDGothicNeoM.ttf") format("truetype");
  font-weight: normal;
}
@font-face { /* 애플산돌고딕R 폰트 적용 */
  font-family: "SDGothicR";
  src: url("./Apple_Neo/AppleSDGothicNeoR.ttf") format("truetype");
  font-weight: normal;
}


*{
  margin:0px;
  padding:0px;
  box-sizing: border-box;
  font-family: 'SDGothicR', serif;
}

body{
  background-color: #5C6792;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
